import React from 'react';
import { Col, Row } from 'antd';
//component import
import MotoCardFrame from '../../UiComponents/HomePage/MotoCardFrame';
//assets import
import think from '../../../assets/think.svg';
import learn from '../../../assets/learn.svg';
import implement from '../../../assets/implement.svg';
import innovate from '../../../assets/innovate.svg';
//------------------------------------------------------------------------
const Moto = () => {
  return (
    <section className='moto'>
      <Row gutter={24}>
        <Col md={4} className='title'>
          <h2>Our Moto</h2>
          <div className='left-circle-border'></div>
        </Col>
        <Col md={20}>
          <Row gutter={12}>
            <MotoCardFrame
              delay='100'
              imgSrc={think}
              header='Think'
              description="Ginnovation muses and reflects the creativity of its' members and the problem.We opine to elucidate the issues nearby."
              id='card-1'
            />
            <MotoCardFrame
              delay='200'
              imgSrc={learn}
              header='Learn'
              id='card-2'
              description="We believe in upgrading ourselves for the cause.We focus on deep learning which is required in today's era to succeed in civic life"
            />
            <MotoCardFrame
              delay='100'
              imgSrc={implement}
              header='Implement'
              id='card-3'
              description='Once leared, have to be implemented.The implementation of the idea or solution is neccessary to resolute.'
            />
            <MotoCardFrame
              delay='300'
              imgSrc={innovate}
              header='Innovate'
              id='card-4'
              description='Ingenuity,originality,talent and resourcefulness is targeted.We focus on the remarkable achievement of the members.'
            />
          </Row>
        </Col>
      </Row>
    </section>
  );
};
export default Moto;
